.header__basket {
    width: 24px;
    height: 24px;
    border: none;
    background-color: transparent;
    background-image: url("../../../images/basket.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 24px 24px;
    margin: 0 28px 0 0;
    position: relative;
    opacity: 1;
    transition: opacity 0.5s ease 0s;
}

.header__basket:hover {
    cursor: pointer;
    opacity: 0.5;
}